import React from 'react';
import RegularTextField from '../../../../shared/RegularTextField';
import { n16_2R } from '../../../../shared/CommonValidators';

const FreightAmountForeign = () => {
  const terms = ['EXW', 'FCA', 'FAS', 'FOB'];
  return (
    terms.includes(localStorage.getItem('tradeTerms')) && (
      <RegularTextField
        name="freightAmountForeign"
        label="Freight Amount Foreign"
        validators={n16_2R.validators}
        errorMessages={n16_2R.validators}
      />
    )
  );
};

export default FreightAmountForeign;
